*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Inter Display', sans-serif;
}

/* CSS for Section1 of Home Starts*/

.Section1-Wrapper{
    display: grid;
    margin: 0 6%;
    grid-template-columns: 2fr 1fr;
    grid-gap: 8px;
}

.Section1-Left , .Section1-Right{
    background-color: #131315;
    padding: 3em;
    color: #ffffff;
    border-radius: 50px;
}

.Section1-Left img{
    width: 7em;
    border-radius: 100px;
}

.Section1-Left h1{
    font-size: 54px;
    font-weight: 500;
    margin-top: 1em;
}

.Section1-Left h1 span{
    color: #8A8A93;
}

.Section1-Right{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3em;
}

.Section1-Right img{
    height: 5em;
}

.Section1-Right h1{
    font-size: 45px;
    font-weight: 500;
    text-align: center;
}

.Section1-Right button{
    padding: 1em 1.5em;
    font-size: 30px;
    border: none;
    outline: none;
    background: #FF5E1A;
    color: #ffffff;
    border-radius: 50px;
}

/* CSS for Section1 of Home Ends*/

/* CSS for Section2 of Home Starts*/

.Section2-Wrapper{
    display: grid;
    grid-template-columns: repeat(6,1fr);
    margin: 8px 6%;
    grid-gap: 8px;
}

.Section2-Menu-wrapper{
    background: #131315;
    padding: 1em;
    border-radius: 50px;
}

.Section2-Wrapper p{
    color: white;
    font-size: 1.2em;
    text-align: center;
}

/* CSS for Section2 of Home Ends*/

/* CSS for Section3 of Home Starts*/

.Section3-Wrapper{
    display: grid;
    margin: 0 6%;
    grid-template-columns: 1fr 2fr;
    grid-gap: 8px;
}

.Section3-Left , .Section3-Right{
    background-color: #131315;
    padding: 3em;
    color: #ffffff;
    border-radius: 50px;
}

.Section3-Left img{
    width: 5em;
    border-radius: 100px;
}

.Section3-Left h1{
