/* CSS for Section3 of Home End*/

/* CSS for Section4 of Home Starts*/

.Section4-Wrapper{
    margin: 0 6%;
    margin-top: 8px;
    background: #131315;
    color: #ffffff;
    border-radius: 50px;
    padding: 1em;
}

.Section4-Header{
    padding: 2em;
}

.Section4-Header{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    align-items: center;
    grid-gap: 1em;
}

.Section4-Image img{
    height: 100%;
    width: 100%;
    border-radius: 34px;
}

.Section4-Header-Title h1{
    font-size: 2.5em;
    font-weight: 500;
}

.Section4-Header-Title p{
    font-size: 1.3em;
    color: #8A8A93;
}

.Section4-Header-Decription p{
    font-size: 1.2em;
}

.Section4-Header-Button{
    text-align: right;
}

.Section4-Header-Button button{
    outline: none;
    border: none;
    font-size: 5em;
    background: transparent;
    color: #ffffff;
    transform: rotate(-45deg);
    cursor: pointer;
    transition: 1s ease-in-out;
    border-radius: 100px;
}

/* CSS for Section4 of Home End*/

